<template>
  <div id="app">
    <h1>My Task Application</h1>
    <div class="content-container">
      <TaskList :tasks="tasks" />
      <SnakeGame />
    </div>
  </div>
</template>

<script>
import TaskList from './components/TaskList.vue';
import SnakeGame from './components/SnakeGame.vue'; // Import SnakeGame

export default {
  name: 'App',
  components: {
    TaskList,
    SnakeGame, // Register SnakeGame
  },
  data() {
    return {
      tasks: [], // Tasks will be fetched from API
    };
  },
  async mounted() {
    try {
      const response = await fetch('/api/tasks'); // Symfony API endpoint
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      this.tasks = await response.json();
    } catch (error) {
      console.error('Error fetching tasks:', error);
    }
  },
};
</script>

