<template>
  <div class="snake-game-container">
    <h2>Snake Game</h2>
    <canvas ref="gameCanvas" :width="canvasWidth" :height="canvasHeight" class="game-canvas"></canvas>
    <div class="game-controls">
      <p>Score: {{ score }}</p>
      <button @click="startGame" v-if="!gameStarted">Start Game</button>
      <button @click="resetGame" v-if="gameOver">Play Again</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SnakeGame',
  data() {
    return {
      canvasWidth: 600, // Increased resolution
      canvasHeight: 600, // Increased resolution
      gridSize: 10, // Keep grid size the same for more cells
      snake: [{ x: 10, y: 10 }],
      food: {},
      direction: 'right',
      gameInterval: null,
      gameSpeed: 100, // ms
      score: 0,
      gameOver: false,
      gameStarted: false,
    };
  },
  computed: {
    context() {
      return this.$refs.gameCanvas.getContext('2d');
    },
  },
  mounted() {
    this.resetGame();
    window.addEventListener('keydown', this.handleKeyDown);
  },
  beforeUnmount() {
    window.removeEventListener('keydown', this.handleKeyDown);
    clearInterval(this.gameInterval);
  },
  methods: {
    startGame() {
      this.gameStarted = true;
      this.gameOver = false;
      this.score = 0;
      this.snake = [{ x: 10, y: 10 }];
      this.direction = 'right';
      this.generateFood();
      this.gameInterval = setInterval(this.gameLoop, this.gameSpeed);
    },
    resetGame() {
      clearInterval(this.gameInterval);
      this.gameStarted = false;
      this.gameOver = true; // Set to true initially so "Play Again" button shows
      this.score = 0;
      this.snake = [{ x: 10, y: 10 }];
      this.direction = 'right';
      this.draw(); // Draw initial state
    },
    gameLoop() {
      if (this.gameOver) return;

      this.moveSnake();
      if (this.checkCollision()) {
        this.gameOver = true;
        clearInterval(this.gameInterval);
        alert(`Game Over! Score: ${this.score}`);
        return;
      }
      this.draw();
    },
    moveSnake() {
      const head = { ...this.snake[0] };
      switch (this.direction) {
        case 'up':
          head.y--;
          break;
        case 'down':
          head.y++;
          break;
        case 'left':
          head.x--;
          break;
        case 'right':
          head.x++;
          break;
      }
      this.snake.unshift(head); // Add new head

      if (head.x === this.food.x && head.y === this.food.y) {
        this.score++;
        this.generateFood();
      } else {
        this.snake.pop(); // Remove tail if no food eaten
      }
    },
    generateFood() {
      let newFood;
      do {
        newFood = {
          x: Math.floor(Math.random() * (this.canvasWidth / this.gridSize)),
          y: Math.floor(Math.random() * (this.canvasHeight / this.gridSize)),
        };
      } while (this.snake.some(segment => segment.x === newFood.x && segment.y === newFood.y));
      this.food = newFood;
    },
    checkCollision() {
      const head = this.snake[0];
      // Wall collision
      if (
        head.x < 0 ||
        head.x >= this.canvasWidth / this.gridSize ||
        head.y < 0 ||
        head.y >= this.canvasHeight / this.gridSize
      ) {
        return true;
      }
      // Self-collision
      for (let i = 1; i < this.snake.length; i++) {
        if (head.x === this.snake[i].x && head.y === this.snake[i].y) {
          return true;
        }
      }
      return false;
    },
    draw() {
      const ctx = this.context;
      ctx.clearRect(0, 0, this.canvasWidth, this.canvasHeight);

      // Draw snake
      this.snake.forEach((segment, index) => {
        // Head is slightly different
        if (index === 0) {
          ctx.fillStyle = '#4CAF50'; // Darker green for head
          ctx.strokeStyle = '#388E3C'; // Border for head
        } else {
          ctx.fillStyle = '#66BB6A'; // Lighter green for body
          ctx.strokeStyle = '#4CAF50'; // Border for body
        }
        ctx.beginPath();
        ctx.roundRect(
          segment.x * this.gridSize,
          segment.y * this.gridSize,
          this.gridSize,
          this.gridSize,
          2 // Rounded corners
        );
        ctx.fill();
        ctx.stroke();
      });

      // Draw food
      ctx.fillStyle = '#FF5722'; // Orange-red for food
      ctx.strokeStyle = '#E64A19'; // Darker border for food
      ctx.beginPath();
      ctx.arc(
        this.food.x * this.gridSize + this.gridSize / 2,
        this.food.y * this.gridSize + this.gridSize / 2,
        this.gridSize / 2 - 1, // Slightly smaller than grid for visual separation
        0,
        2 * Math.PI
      );
      ctx.fill();
      ctx.stroke();
    },
    handleKeyDown(event) {
      if (this.gameOver && event.key === 'Enter') {
        this.startGame();
        return;
      }
      if (!this.gameStarted && event.key === 'Enter') {
        this.startGame();
        return;
      }

      const newDirection = event.key.replace('Arrow', '').toLowerCase();
      const oppositeDirections = {
        up: 'down',
        down: 'up',
        left: 'right',
        right: 'left',
      };

      if (newDirection && newDirection !== oppositeDirections[this.direction]) {
        this.direction = newDirection;
      }
    },
  },
};
</script>

